.block{
  width: 100%;
  .ingredient {
    h2, h3 {
      text-align: center;
    }

    .ingredientBlock {
      display: flex;
      justify-content: space-around;
      align-items: center;

      .description {
        margin: 20px;
        max-width: 600px;
        text-align: justify;
      }
    }

    .drinksBlock {
      display: flex;
      flex-wrap: wrap;
      a {
        text-decoration: none;
        .drink {
        margin: 10px;
        width: 200px;
          p {
            color: darksalmon;
            text-align: center;
          }
      }
      }
    }
  }
}